import { isNotInUni } from './utils';


export function routerBack() {

  if (isNotInUni()) {
    // @ts-ignore
    this.$router.back();
    return;
  }

  uni.navigateBack();
}


export function routerPush(url, notUniUrl) {

  if (isNotInUni()) {
    // @ts-ignore
    this.$router.push(notUniUrl || url);
    return;
  }

  uni.navigateTo({
    url,
  });
}

