import { isNotInUni } from '../common/utils/utils';

export function getShowDemoMap() {
  const showOtherDemoMap = {
    vue2Uni: true,
    vue2NotUni: true,
    vue3Uni: true,
    vue3NotUni: true,
  };


  if (isNotInUni()) {
    showOtherDemoMap.vue2NotUni = false;
  } else {
    showOtherDemoMap.vue2Uni = false;
  }











  return showOtherDemoMap;
}
